@import "../../scss//base.scss";



.container {                                                           //main container for home page
    display: grid;
    grid-template-rows: 100vh  min-content 40vw repeat(3, min-content);
    grid-template-columns:
        [full-start] minmax(6rem, 1fr) [center-start] repeat(
            8,
            [col-start] minmax(min-content, 14rem) [col-end]
        )
        [center-end] minmax(6rem, 1fr) [full-end];

    @media only screen and (max-width: $bp-large) {
        grid-template-rows:  80vh min-content 40vw repeat(3, min-content);
        grid-template-columns:
            [full-start] minmax(6rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 14rem) [col-end])
            [center-end] minmax(6rem, 1fr) [full-end];
    }


    @media only screen and (max-width: $bp-medium) {
        grid-template-rows:  calc(100vh - 6rem);
    }
}
