$color-primary: #c69963;
$color-primary-dark: #b28451;

$color-secondary: #101d2c;

$color-gray-light-1: #f9f7f6;
$color-gray-light-2: #aaa;

$color-gray-dark-1: #54483a;
$color-gray-dark-2: #6d5d4b;

$font-primary: "Nunito", sans-serif;
$font-display: "Josefin Sans", sans-serif;


//responcive break point
$bp-largest: 75em; //1200px
$bp-large: 62.5em; //1000px
$bp-medium: 50em; //800px
$bp-small: 37.5em; //600px  600/16
//heading
%heading {
    font-family: $font-display;
    font-weight: 400;
}
.heading-1 {
    @extend %heading;
    font-size: 4.5rem;
    color: $color-gray-light-1;
    line-height: 1;
}
.heading-2 {
    @extend %heading;
    font-size: 4rem;
    font-style: italic;
    line-height: 1;
    &__light {
        color: $color-gray-light-1;
    }
    &__dark {
        color: $color-gray-dark-1;
    }
}
.heading-3 {
    @extend %heading;
    font-size: 1.6rem;
    color: $color-primary;
    text-transform: uppercase;
}
.heading-4 {
    @extend %heading;
    font-size: 1.9rem;
    &__light {
        color: $color-gray-light-1;
    }
    &__dark {
        color: $color-gray-dark-1;
    }
}
.btn {
    background-color: $color-primary;
    color: #fff;
    border: none;
    border-radius: 0;
    font-family: $font-display;
    font-size: 1.5rem;
    text-transform: uppercase;
    padding: 1.8rem 3rem;
    cursor: pointer;
    transition: all 0.2s;
outline: none;
    &:hover {
        background-color: $color-primary-dark;
    }
}
.mb-sm {
    margin-bottom: 2rem;
}
.mb-md {
    margin-bottom: 3rem;
}
.mb-lg {
    margin-bottom: 4rem;
}
.mb-hg {
    margin-bottom: 8rem;
}
