@import "../../scss/base.scss";

.footer {
    background-color: $color-secondary;
    grid-column: full-start/full-end;
    padding: 8rem;
}
.nav {
    list-style: none;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
    grid-gap: 2rem;
    align-items: center;
    &__link:link,
    :visited {
        color: #fff;
        font-size: 1.4rem;
        text-decoration: none;
        font-family: $font-display;
        text-transform: uppercase;
        text-align: center;
        padding: 1.5rem;
        display: block;
        transition: all 0.2s;
    }
    &__link:hover,
    &__link:active {
        background-color: rgba(#fff, 0.05);
        transform: translateY(-3px);
    }
}
.copyright {
    font-size: 1.4rem;
    color: $color-gray-light-2;
    margin-top: 6rem;
    text-align: center;
    width: 70%;
    margin-right: auto;
    margin-left: auto;
}

